        "4. Construir un cuadrado de 4x4 el cual debe de estar dentro de un cuadrado de 12x12 (tomar una foto a la construcción y colocarla en el recuadro)."
      ],
      section3Info:[
        ["Rectángulo",""],
        ["Cuadrado",""]
      ],
      section3Opinion:[
        "","","","",
        "","","","",
        "","","",""
