      photoPlaceholder: require("@/assets/images/activities-assets/activity_1-3/Recurso 78@4x.png"),
      photoPlaceholder2: require("@/assets/images/activities-assets/activity_1-3/Recurso 75@4x.png"),
      photoPlaceholder3: require("@/assets/images/activities-assets/activity_1-3/Recurso 73@4x.png")
    };
  },
  methods: {
    onFileChange(e, i) {
      console.log(i)
      const file = e.target.files[0];
      const reader = new FileReader();
      this.photos[i].file = file;
      reader.onload = (e) => {
        this.photos[i].source = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    getPlaceholder(i){
      return i == 1 ? this.photoPlaceholder2 : this.photoPlaceholder3
    },
    getIndex(i,j){
      switch(i){
        case 1: return i+j-2;
        case 2: return i+j;
        case 3: return i+j+2;
        case 4: return i+j+4
      }
    }
  },
};
</script>

